import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';

import {environment} from '../../environments/environment';

@Injectable({
    providedIn: 'root'
})


export class ApiService {
    /**
     * A shared service to manage the client's authentication and connection to the backend API.
     */

    API_URL = environment.apiUrl;
    TOKEN_VALID_FOR = 60 * 60 * 24;
    // Pre-fill the username box
    username = 'admin';
    password = '';

    // http options used for making API calls
    private httpOptions: any;

    // the actual JWT token
    public token: string;

    // the token expiration date
    public token_expires: Date;

    // error messages received from the login attempt
    public errors: any = [];

    constructor(private http: HttpClient) {
        this.httpOptions = {
            headers: new HttpHeaders({'Content-Type': 'application/json'})
        };
    }

    // Functions using /logs endpoint

    public getTradingLogs(): Observable<String[]> {
        this.ensureValidToken();
        try {
            return this._getTradingLogs();
        } catch (e) {
            this.refreshToken();
            return this._getTradingLogs();
        }
    }

    private _getTradingLogs() {
        return this.http.get<String[]>(`${this.API_URL}/logs/trading`,
            {
                headers: new HttpHeaders().set('Authorization', `Bearer ${this.token}`)
            });
    }

    // Authentication functions

    private ensureValidToken(): void {
        if (this.token == null
            || this.token_expires.getTime() <= new Date(new Date().getTime() - 5 * 1000).getTime()) {
            this.refreshToken();
        }
    }

    public login() {
        /**
         * Gets an auth token from django.
         */
        console.log(this.http.post(`${this.API_URL}/token/new`, JSON.stringify({
            username: this.username,
            password: this.password
        }), this.httpOptions).subscribe(
            data => {
                this.updateData(data['token']);
            },
            err => {
                console.log('auth token request received error response:');
                console.log(err);
            }
        ));
    }

    // Refreshes the JWT token, to extend the time the user is logged in
    public refreshToken() {
        this.http.post(`${this.API_URL}/token/refresh`, JSON.stringify({token: this.token}),
            this.httpOptions).subscribe(
            data => {
                this.updateData(data['access']);
            },
            err => {
                this.errors = err['error'];
            }
        );
    }

    public logout() {
        this.token = null;
        this.token_expires = null;
    }

    private updateData(token) {
        this.token = token;
        this.token_expires = new Date(new Date().getTime() + (1000 * this.TOKEN_VALID_FOR));
    }
}
