import {Component, OnInit} from '@angular/core';
import {ApiService} from "../../shared_service/api.service";
import {TabService, TabSubType} from "../../shared_service/tab.service";

@Component({
    selector: 'app-login-content',
    templateUrl: './login-content.component.html',
    styleUrls: ['./login-content.component.less']
})
export class LoginContentComponent implements OnInit {
    constructor(private api: ApiService, private tabs: TabService) {
    }

    ngOnInit() {
    }

    login() {
        this.api.login();
        this.tabs.selectSubTab(TabSubType.console);
    }

    getTradingLogs() {
        let logs = this.api.getTradingLogs();
        console.log(logs);
    }
}
